import { Component, OnInit } from '@angular/core';
import { AngularFire, FirebaseListObservable } from 'angularfire2';
import { MdSnackBar } from '@angular/material';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise'



@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  public quote: Quote; // our model
  projects: FirebaseListObservable<any[]>;
  testimonials: FirebaseListObservable<any[]>;
  qouteRequests: FirebaseListObservable<any[]>;
  contactUs: FirebaseListObservable<any[]>;
  public loading: boolean;




  constructor(public af: AngularFire, public snackBar: MdSnackBar, private http: Http) {
    this.projects = af.database.list('/projects');
    this.testimonials = af.database.list('/testimonials');
    this.qouteRequests = af.database.list('/quoteRequests');
    this.contactUs = af.database.list('/contactUs');
  }


  ngOnInit() {
    this.quote = {
      name: "",
      email: "",
      message: ""
    };

    this.loading = false
  }


  quoteRequest(model: Quote, isValid: boolean) {
    this.loading = true
    var self = this;

    var body = {
      email: model.email,
      message: model.name,
      name: model.message
    }
    var url = 'https://habilomune-1dbf5.firebaseio.com/quoteRequests.json';
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });

    return this.http.post(url, body, options)
      .map((res: Response) => res.json())
      .toPromise()
      .then(function (res) {
        self.loading = false
        // console.log(this.loading)
      })
      .catch(function (err) {
        self.loading = false
        // console.log(this.loading)
      })
  }


  feedback(frm) {
    this.loading = true
    var self = this;
    var body = {
      email: frm.email,
      message: frm.name,
      name: frm.feedback
    }
    var url = 'https://habilomune-1dbf5.firebaseio.com/feedbackforms.json';
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });

    return this.http.post(url, body, options)
      .map((res: Response) => res.json())
      .toPromise()
      .then(function (res) {
        self.loading = false

      })
      .catch(function (err) {
        self.loading = false

      })
  }








}

export interface Quote {
  name: "",
  email: "",
  message: ""
}



